							}
						},
						{
							title: '申请原因',
							key: 'outReason',
							align: 'center'
						},
						{
							title: '押品类型',
							key: 'gmType',
							align: 'center'
						},
						{
							title: '评估金额（万）',
							key: 'mhouseValue',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								return h('span', _this.fmoney(params.row.mhouseValue / 10000))
							}
						},
						{
							title: '产权人',
							key: 'propertyOwner',
							align: 'center'
						},
						{
							title: '押品状态',
							key: 'gmStatus',
							align: 'center',
							render: (h, params) => {
								if (params.row.gmStatus == '5') {
									return h('span', {
										style: {
											color: '#19be6b'
										}
									}, '已出库')
								} else if (params.row.gmStatus == '4') {
									return h('span', {
										style: {
											color: '#ed3f14'
										}
									}, '审核中')
								}else if(params.row.gmStatus == '6'){
									return h('span', {
										style: {
											color: '#FF9900'
										}
									}, '出库被拒')
								}
							}
						}
					],
					historyList: [],
					dateRange: [],
					totalPage: 10,
					searchParams: {
						aBaseName: '',
						loanId: '',
						startTime: '',
						endTime: '',
						currentPage: 1,
						pageSize: 10
					}
				},
				tab: 'out',
				refreshFlag:true
			}
		},
		created: function () {
			let tab = this.$route.params.tab;
			if (tab) {
				this.tab = tab;
			}
			this.loadList();
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				if (_this.tab == 'out') {//出库申请切换页码
					if (_this.outTab.searchParams.currentPage != val) {
						_this.outTab.searchParams.currentPage = val;
						_this.loadList();
					}
				} else if (_this.tab == 'stock') {//库存中
					if (_this.stockTab.searchParams.currentPage != val) {
						_this.stockTab.searchParams.currentPage = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.currentPage != val) {
						_this.historyTab.searchParams.currentPage = val;
						_this.loadList();
					}
				}
			},
			pageSizeChange: function (val) {
				let _this = this;
				if (_this.tab == 'out') {//出库申请切换页码大小
					if (_this.outTab.searchParams.pageSize != val) {
						_this.outTab.searchParams.pageSize = val;
						_this.loadList();
					}
				} else if (_this.tab == 'stock') {
					if (_this.stockTab.searchParams.pageSize != val) {
						_this.stockTab.searchParams.pageSize = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.pageSize != val) {
						_this.historyTab.searchParams.pageSize = val;
						_this.loadList();
					}
				}
			},
			toggleTab: function (name) {
				this.refreshFlag=true;
				this.loadList();
			},
			loadList: function () {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				if (_this.tab == 'out') {//出库申请
					_this.$http.post('/Storage/passComeStorage', _this.outTab.searchParams)
						.then(function (response) {
							var res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.outTab.totalPage = res.data.count;
									_this.outTab.searchParams.currentPage = res.data.currentPage;
									_this.outTab.searchParams.pageSize = res.data.pageSize;
									_this.outTab.outList = res.data.list;
								}
							} else {
								_this.$Message.error({
									content: res.msg
								});
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							});
						});
				} else if (_this.tab == 'stock') {//库存中
					this.$http.post('/Storage/inStorage', _this.stockTab.searchParams)
						.then(function (response) {
							let res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.stockTab.totalPage = res.data.count;
									_this.stockTab.searchParams.currentPage = res.data.currentPage;
									_this.stockTab.searchParams.pageSize = res.data.pageSize;
									_this.stockTab.stockList = res.data.list;
								}
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							})
						});
				} else if (_this.tab == 'history') {//历史记录
					this.$http.post('/Storage/outComeStorage', _this.historyTab.searchParams)
						.then(function (response) {
							let res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.historyTab.totalPage = res.data.count;
									_this.historyTab.searchParams.currentPage = res.data.currentPage;
									_this.historyTab.searchParams.pageSize = res.data.pageSize;
									_this.historyTab.historyList = res.data.list;
								}
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							})
						});
				}
			},
			dateChange: function (range) {
				let _this = this;
				if (_.includes(range, undefined)) {
					_this.historyTab.searchParams.startTime = '';
					_this.historyTab.searchParams.endTime = '';
				} else {
					_this.historyTab.searchParams.startTime = range[0];
					_this.historyTab.searchParams.endTime = range[1];
				}
			},
			search: function () {
				let _this = this;
				//清空页码
				if (_this.tab == 'out') {
					_this.outTab.searchParams.currentPage = 1;
					_this.outTab.searchParams.pageSize = 10;
				} else if (_this.tab == 'stock') {
					_this.stockTab.searchParams.currentPage = 1;
					_this.stockTab.searchParams.pageSize = 10;
				} else if (_this.tab == 'history') {
					_this.historyTab.searchParams.currentPage = 1;
					_this.historyTab.searchParams.pageSize = 10;
				}
				//搜索
				_this.refreshFlag=true;
				_this.loadList();
			},
			refresh: function () {
				this.refreshFlag=true;
				this.loadList();
			},
			clearAll: function () {//清空搜索，翻页等信息
				if (this.tab == 'out') {
					this.outTab.searchParams.currentPage = 1;
					this.outTab.searchParams.pageSize = 10;
					this.outTab.searchParams.aBaseName = '';
					this.outTab.searchParams.loanId = '';
				} else if (this.tab == 'stock') {
					this.stockTab.searchParams.currentPage = 1;
					this.stockTab.searchParams.pageSize = 10;
					this.stockTab.searchParams.aBaseName = '';
					this.stockTab.searchParams.loanId = '';
				} else if (this.tab == 'history') {
					this.historyTab.dateRange = [];
					this.historyTab.searchParams.aBaseName = '';
					this.historyTab.searchParams.loanId = '';
					this.historyTab.searchParams.startTime = '';
					this.historyTab.searchParams.endTime = '';
					this.historyTab.searchParams.currentPage = 1;
					this.historyTab.searchParams.pageSize = 10;
				}

			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.name .ivu-input-group-prepend {
		background-color: #20abff;
		border-color: #20abff;
		width: 80px;
	}

	.orderNum .ivu-input-group-prepend {
		background-color: #f0b320;
		border-color: #f0b320;
		width: 80px;
	}

	.input-title {
		color: #fff;
		font-size: 14px;
	}

	.search-box {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding-top: 14px;
		padding-bottom: 15px;
		flex-wrap: wrap;
	}

	.search-item {
		flex: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
		margin-right: 10px;
	}

	.item-title {
		width: 80px;
		height: 36px;
		font-size: 14px;
		line-height: 36px;
		text-align: center;
		color: #fff;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
	}

	.color-yellow {
		background-color: #f0b320;
	}

	.color-red {
		background-color: #ff7043;
	}

	.color-green {
		background-color: #28b779;
	}

	.search {
		width: 100px;
		margin-right: 10px;
	}

	.ivu-select-selection {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}

	.search-item .ivu-input {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
</style>



